import * as Realm from 'realm-web';
import { useEffect, useState } from 'react';
import Login from '../Login/Login';
import ChatPage from '../ChatPage/ChatPage';
export default function Home(props){
    const [page,setPage]=useState('Login');
    const [realmObject,setRealmObject]=useState({});
    const authenticate=async (appId,username,password)=>{
        let app=new Realm.App({id:appId});
        let credentials=Realm.Credentials.emailPassword(username,password);
        let user=await app.logIn(credentials);
        console.assert(user.id === app.currentUser.id);
        setRealmObject({
            app:app,
            user:user
        });
        //app.currentUser.mongoClient().db().collection().watch();
        setPage('Home');
    };
    useEffect(()=>{
        let JWT=sessionStorage.getItem("JWT");
        if(JWT){
            
        }
    },[]);
    return (
        <div>
            {page === 'Login' && <Login authenticate={authenticate}/> }
            {page === 'Home' && <ChatPage realmObject={realmObject}/>}
        </div>
    );
}