import { useEffect, useState } from "react";
import styles from './SearchableDropdown.module.scss';
export default function SearchableDropDown(props){
    const [showDropdown,setShowDropdown]=useState(false);
    const [options,setOptions]=useState([]);
    const [input,setInput]=useState('');
    const [mouseIn,setMouseIn]=useState(false);
    useEffect(()=>{
        if(input){
            props.mongoClient.collection('chat_users').aggregate([
                {$search:{
                    index:'default',
                    text:{
                        query:input,
                        path:{
                            wildcard:'*'
                        }
                    }
                }},{
                    $limit:6
                }
            ]).then((res)=>{console.log(res);setOptions(res)});
        }
    },[input]);
    return (
        <div className={`${styles.forwholediv} ${styles.forelementwithlabel}`} onBlur={()=>{if(!mouseIn)setShowDropdown(false)}}>
            <input type='text' onChange={(event)=>{setShowDropdown(true);setInput(event.target.value)}} value={input}
                onFocus={()=>{setShowDropdown(true)}} 
                placeholder={'Search'}
            />
            {showDropdown && 
                <div onMouseEnter={()=>{setMouseIn(true)}} onMouseLeave={()=>setMouseIn(false)} className={styles.fordropdown}>
                    {options.map((eachOption,index)=>{
                        return (
                            <button className={styles.fordropdownelement} key={index} onClick={()=>{
                                props.onChange(eachOption);
                                setShowDropdown(false);
                                setInput('');
                            }}>{`${eachOption.username}\n${eachOption.email}`}</button>
                        );
                    })}
                </div>
            }
        </div>
    );
}