import { useEffect, useState } from "react";
import styles from './Messages.module.scss';
export default function Messages(props) {
    const [textToSend, setTextToSend] = useState('');
    const [chatData, setChatData] = useState([...props.selectedUser.chatDataOfUser]);
    useEffect(() => {
        console.log('dataChanged')
        setTextToSend(props.textBuffer ? props.textBuffer + '' : '');
        setChatData([...props.selectedUser.chatDataOfUser]);
    }, [props.selectedUser.chatDataOfUser]);
    return (
        <div style={{position:"relative"}}>
            <div className={styles.forcontainer}>
                {chatData.length > 0 &&
                    chatData.map((eachChatData, index) => {
                        return (
                            <div key={index} style={{ backgroundColor: "pink", margin: "1% 0%", alignSelf: eachChatData.from ? 'start' : 'end' }}>
                                <p>{new Date(eachChatData.date).toLocaleString()}</p>
                                <p>{eachChatData.text}</p>
                            </div>
                        )
                    })
                }
            </div>
            <div className="">
                <input type={'text'} onChange={(event) => { setTextToSend(event.target.value) }} value={textToSend} />
                <button type="button" onClick={async () => {
                    if(!textToSend){
                        return;
                    }
                    await props.sendMessageToFriend(textToSend);
                    setTextToSend('');
                }}>Send</button>
            </div>
        </div>
    );
}